<template>
    <div class="rightTable">
        <div class="top">
            <span>热门商品</span>
            <ul>
                <li v-for="good in tableGoods" @click="addfoodList(good)">
                    {{good.goodsName}} ￥{{good.price}}
                </li>
            </ul>
        </div>
        <div class="bottom">
            <el-tabs :stretch="true">
                <el-tab-pane label="汉堡">
                    <ul>
                        <li v-for='good in table0Goods' @click="addfoodList(good)">
                            {{good.goodsName}} ￥{{good.price}}
                        </li>
                    </ul>
                </el-tab-pane>
                <el-tab-pane label="小食">
                    <ul>
                        <li v-for='good in table1Goods' @click="addfoodList(good)">
                            {{good.goodsName}} ￥{{good.price}}
                        </li>
                    </ul>
                </el-tab-pane>
                <el-tab-pane label="饮料">
                            <ul>
                        <li v-for='good in table2Goods' @click="addfoodList(good)">
                            {{good.goodsName}} ￥{{good.price}}
                        </li>
                    </ul>
                </el-tab-pane>
                <el-tab-pane label="套餐">
                    <ul>
                        <li v-for='good in table3Goods' @click="addfoodList(good)">
                            {{good.goodsName}} ￥{{good.price}}
                        </li>
                    </ul>
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import bus from "../../bus.js";
export default {
  name: "rightTable",
  data() {
    return {
      tableGoods: [],
      table0Goods: [],
      table1Goods: [],
      table2Goods: [],
      table3Goods: [],
      foodsTable: [],
      totalPrice: 0,
      totalCount: 0
    };
  },
  created() {
    axios
      .get(
        "https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods"
      )
      .then(response => {
        this.tableGoods = response.data;
      })
      .catch(error => {
        alert("数据请求失败！");
      });
    axios
      .get(
        "https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/typeGoods"
      )
      .then(response => {
        this.table0Goods = response.data[0];
        this.table1Goods = response.data[1];
        this.table2Goods = response.data[2];
        this.table3Goods = response.data[3];
      })
      .catch(error => {
        alert("数据请求失败！");
      });
  },
  mounted: function() {
    var vm = this;
    bus.$on("changeOther", function(val) {
      vm.foodsTable = val;
    });
  },
  methods: {
    addfoodList: function(food) {
      let isHave = false;
      if (this.foodsTable.length > 0) {
        for (let i = 0; i < this.foodsTable.length; i++) {
          if (this.foodsTable[i].goodsId === food.goodsId) {
            isHave = true;
            this.foodsTable[i].count++;
          }
        }
      }
      if (!isHave) {
        this.foodsTable.push({
          goodsId: food.goodsId,
          goodsName: food.goodsName,
          count: 1,
          price: food.price
        });
      }
      bus.$emit("change", this.foodsTable);
    }
  }
};
</script>

<style>
.rightTable {
  width: 65%;
  height: 100%;
  float: left;
  background-color: #fff;
}
.top span {
  width: 100%;
  text-align: center;
  height: 30px;
  line-height: 30px;
  font-size: 24px;
  display: block;
}
.top ul li,
.bottom ul li {
  list-style: none;
  background-color: #409eff;
  color: #fff;
  font-size: 18px;
  width: 100px;
  height: 50px;
  text-align: center;
  margin: 30px;
  padding: 6px;
  float: left;
}

.bottom {
  clear: both;
}
</style>
