<template>
    <div class="mainNav">
        <el-tabs type="card" :stretch="true">
            <el-tab-pane label="点餐" class="orderTable">
                <el-table :data="foodsTable" border style="width: 100%" class="table">
                    <el-table-column label="商品名称" prop="goodsName"></el-table-column>
                    <el-table-column label="数量" prop="count"></el-table-column>
                    <el-table-column label="价格" prop="price"></el-table-column>
                    <el-table-column label="操作" fixed="right">
                        <template scope="scope">
                            <el-button type="text" @click="delFood(scope.row)">删除</el-button>
                            <el-button type="text" @click="addFood(scope.row)">增加</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="total">
                    <span>总数：{{totalCount}}</span> <span>总消费：{{totalPrice}}</span>
                </div>
                <div class="function">
                    <el-button type="info">挂单</el-button>
                    <el-button type="warning" @click="delAll">清空</el-button>
                    <el-button type="primary" @click="checkOut">结账</el-button>
                </div>
                
            </el-tab-pane>
            <el-tab-pane label="挂单">挂单</el-tab-pane>
            <el-tab-pane label="外卖">外卖</el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import bus from "../../bus.js";
export default {
  name: "mainNav",
  data() {
    return {
      totalCount1: 0,
      totalPrice1: 0,
      foodsTable: []
    };
  },
  mounted: function() {
    var vm = this;
    bus.$on("change", function(val) {
      vm.foodsTable = val;
    });
  },
  methods: {
    addFood: function(food) {
      this.foodsTable.forEach(e => {
        if (food.goodsId === e.goodsId) {
          e.count++;
        }
      });
    },
    delFood: function(food) {
      this.foodsTable = this.foodsTable.filter(e => {
        return food.goodsId != e.goodsId;
      });
    },
    delAll: function() {
      this.$confirm("此操作将清空订单列表，是否继续？", "提示", {
        confirmButtonText: "确定清空",
        cancelButtonText: "放弃清空",
        type: "warning"
      }).then(() => {
        this.$message({
          type: "success",
          message: "清空订单列表成功！"
        });
        this.foodsTable = [];
      }).catch(()=>{
          this.$message({
              type:"info",
              message: "点错了，那就不清空了！"
          })
      })
    },
    checkOut: function() {
      this.$confirm("此操作将提交订单，是否继续？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "提交成功，顾客本次消费：" + this.totalPrice + " 软妹币"
          });
          this.foodsTable = [];
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已放弃提交订单"
          });
        });
    }
  },
  computed: {
    totalCount: function() {
      this.totalCount1 = 0;
      this.foodsTable.forEach(e => {
        this.totalCount1 += e.count;
      });
      bus.$emit("changeOther", this.foodsTable);
      return this.totalCount1;
    },
    totalPrice: function() {
      this.totalPrice1 = 0;
      this.foodsTable.forEach(e => {
        this.totalPrice1 += e.count * e.price;
      });
      return this.totalPrice1;
    }
  }
};
</script>

<style>
.mainNav {
  float: left;
  width: 30%;
  height: 100%;
  text-align: center;
}
.function {
  margin-top: 10px;
}
.total {
  padding: 9px;
}
.total span:first-child::after {
  display: inline-block;
  content: "";
  padding-right: 20px;
}
</style>
